<template>
  <div class="row my-bargain" style="padding:0; margin: 0; width: 100%;">
    <router-view @onAddedSuccess="onPageChange" :deal="editDeal"></router-view>
    <div class="col-lg-12 reset-col">
      <div class="card" style="padding-bottom: 5px; border-top: 3px solid #03A9F4;">
        <div class="row">
          <ul class="nav nav-tabs" style="width: 100%">
            <li class="nav-item">
              <a class="nav-link active" href="#"
                 style="border-radius:0; font-size:13px; padding-right: 10px; padding-left: 10px;"><span
                style="font-size: 13px;"><b> My Bargains </b></span></a>
            </li>
          </ul>
        </div>
        <div class="bargain-route row" style="padding-left:10px; padding-right: 10px;">
          <div class="col-lg-12 reset-col" style="margin-top: 10px;">
            <router-link :to="{name: 'addBargain'}">
              <button type="button" class="btn btn-raised btn-success btn-sm pull-left">
                <i class="fa fa-plus" aria-hidden="true" style="padding-right:5px;"></i>Add Bargain

              </button>
            </router-link>
            <div class="col-lg-3 reset-col pull-right">
              <datatable-search v-model="search"></datatable-search>
            </div>
          </div>
          <div class="col-lg-12 reset-col">
            <datatable
              ref="table"
              dom="rti"
              :ajax="getData"
              :columns="columns"
              :search="search"
              v-on:table="tableMethod"
              :columnDef="colDef"
              :per-page="pageSize"
              :server-side="false"
              :order="order"
              :target="myObject"
            ></datatable>
            <el-pagination
              @current-change="onPageChange"
              :current-page="currentPage"
              :page-size="pageSize"
              layout="prev, pager, next"
              :total="pageCount"
              class="pull-right">
            </el-pagination>
          </div>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
  import {db} from '../../../firebase/vueFire'
  import fireBase from '../../../firebase/vueFire'
  import {Message, Pagination} from 'element-ui'
  import moment from 'moment'
  import {checkAuth} from '../../../util/auth-helper'
  import _ from 'lodash'
  export default {
    name: 'UserBargains',
    components: {
      elPagination: Pagination
    },
    data () {
      return {
        title: "",
        bargainType: null,
        percent: null,
        listPrice: null,
        salePrice: null,
        stores: [],
        search: '',
        selectedCategory: [],
        selectedStore: null,
        selectedType: null,
        validOn: null,
        validUntil: null,
        editDeal: {},
        fileList: [],
        mainUrl: '',
        drop: true,
        multiple: true,
        fpOptions: {
          enableTime: true,
          minuteIncrement: 1,
          altInput: true,
          altFormat: 'F j, Y H:m:i'
        },
        enabled: false,
        myObject: this,
        pageSize: 50,
        id: null,
        origin: process.env.API_PATH,
        path: process.env.API_PATH + '/user/bargains',
        selected: "",
        tagsValue: [],
        value: "",
        events: {
          add () {
          }
        },
        currentPage: 1,
        openModal: true,
        pageCount: 1,
        order: [[1, 'desc']],
        content: "",
        dataTable: {},
        formats: [
          'background',
          'bold',
          'color',
          'font',
          'code',
          'italic',
          'link',
          'size',
          'strike',
          'script',
          'underline',
          'blockquote',
          'header',
          'indent',
          'list',
          'align',
          'direction',
          'code-block',
          'formula'
        ],
        toolbarOptions: [
          ['bold', 'italic', 'underline', 'strike'],        // toggled buttons
          ['blockquote', 'code-block'],
          [{'header': 1}, {'header': 2}],               // custom button values
          [{'list': 'ordered'}, {'list': 'bullet'}],
          [{'script': 'sub'}, {'script': 'super'}],      // superscript/subscript
          [{'indent': '-1'}, {'indent': '+1'}],          // outdent/indent
          [{'direction': 'rtl'}],                         // text direction
          [{'size': ['small', false, 'large', 'huge']}],  // custom dropdown
          [{'header': [1, 2, 3, 4, 5, 6, false]}],
          [{'color': []}, {'background': []}],          // dropdown with defaults from theme
          [{'font': []}],
          [{'align': []}],
          ['clean']                                         // remove formatting button
        ],
        typeOptions: [{
          value: 'sale',
          label: 'sale'
        }, {
          value: 'percentOff',
          label: 'percentOff'
        }, {
          value: 'free',
          label: 'free'
        }, {
          value: 'promotion',
          label: 'promotion'
        }, {
          value: 'other',
          label: 'other'
        }],
        categoryOptions: ['Electronic', 'Clothes', 'Glasses', 'Watches', 'Software'],
        options5: [{
          value: 'Electronic',
          label: 'Electronic'
        }, {
          value: 'Computer',
          label: 'Computer'
        }, {
          value: 'Laptop',
          label: 'Laptop'
        }],
        editorOption: {
          formats: this.formats,
          modules: {
            toolbar: this.toolbarOptions
          }
        },
        storeOptions: [],
        options: ['Newest to Oldest', 'Oldest to Newest'],
        tagOptions: ['Electronic', 'Computer', 'Laptop'],
        colDef: [
          {'width': '3%', 'class': 'text-center', 'targets': 3},
          {'width': '3%', 'class': 'text-center', 'targets': 4},
          {'width': '3%', 'class': 'text-center', 'targets': 5},
          {'width': '3%', 'class': 'text-center', 'targets': 6},
        ]
      }
    },
    mounted () {
      this.$root.$on('hidden::modal', (id) => {
        this.storeOptions = []
        this.title = null
        this.content = ""
        this.selectedCategory = []
        this.selectedType = null
        this.validOn = null
        this.validUntil = null
        this.mainUrl = null
        this.tagsValue = []
        this.listPrice = null
        this.fileList = []
        this.salePrice = null
        this.percent = null
        this.selectedStore = {}
        this.errors.clear()
      })
    },
    methods: {
      isDisabled(value) {
        if (this.selectedType) {
          if (this.selectedType.value === value) {
            return false
          } else {
            return true
          }
        } else {
          return true
        }
      },
      closeCustom () {
        this.$root.$emit('hide::modal', 'customModal')
      },
      closeEdit () {
        this.$root.$emit('hide::modal', 'customEdit')
      },
      onPageChange () {
        this.tableObject.ajax.reload()
      },
      updateStatus (data, args, input) {
        this.$http.put(this.path + '/' + data._id + '/edit-status/' + input).then((response) => {
          this.tableObject.ajax.reload()
        }).catch((error) => {
          this.$root.$refs.toastr.Add({
            title: "",
            msg: error.body.message,
            clickClose: true,
            timeout: 4000,
            position: "toast-top-right",
            type: "error"
          })
        })
      },
      tableMethod (data) {
        this.tableObject = data
      },
      deleteAction (user) {
        this.$swal({
          title: 'Are you sure?',
          text: 'You will not be able to recover this bargain!',
          type: 'warning',
          showCancelButton: true,
          confirmButtonText: 'Yes, delete it!',
          cancelButtonText: 'Cancel'
        }).then(() => {
          this.$http.delete(this.path + '/' + user._id).then((response) => {
            Message({
              showClose: true,
              message: 'Successfully Deleted Bargain',
              type: 'success'
            })
            this.tableObject.ajax.reload()
          }, (error) => {
            Message({
              showClose: true,
              message: error.body.message,
              type: 'error'
            })
          })
        })
      },
      editAction (user) {
        this.editDeal = user
        this.$router.push({name: 'editBargain'})
      },
      getData (data, callback, settings) {
        var header = 'Bearer '
        var token = checkAuth()
        var store = this.$store
        var router = this.$router
        if (token === undefined) {
          this.$router.push({path: '/'})
        } else {
          header += token
        }
        settings.jqXHR = $.ajax({
          'type': 'GET',
          'url': process.env.API_PATH + '/user/bargains',
          'data': {
            limit: this.pageSize,
            page: this.currentPage
          },
          'beforeSend': function (request) {
            request.setRequestHeader('Authorization', header)
          },
          'success': function (value) {
            var result = {data: value.docs}
            callback(result)
          },
          'error' (value) {
            console.log(value)
            fireBase.auth().signOut().then(() => {
              store.dispatch('clearUser')
              router.push({path: '/'})
            })
          }
        }).done((data) => {
          this.pageCount = data.total
          this.dataTable = data.docs
        })
      },
      updateBargainStatus (data, args, input) {
        this.$http.put(this.path + data._id + '/bargain-status/' + input).then((response) => {
          console.log(response)
          this.tableObject.ajax.reload()
        }).catch((error) => {
          this.$notify.error({
            title: 'Error',
            message: error.body.message,
            duration: 1500
          })
        })
      }
    },
    computed: {
      columns () {
        var column = [
          {
            name: 'title',
            data: 'title',
            title: 'Title',
            render: function (data) {
              return '<a href="#">' + data + '</a>'
            }
          },
          {
            name: 'dateAdded',
            title: 'Date Added',
            render: function (data, type, full, meta) {
              return moment(data).format('YYYY/MM/DD')
            }
          },
          {
            name: 'type', data: 'type', title: 'Type',
            render: function (data, type, full, meta) {
              return '<span style="text-transform: capitalize; font-size:12px;">' + data + '</span>'
            }
          },
          {
            name: 'editStatus',
            data: 'editStatus',
            title: 'Edit Status',
            click: [this, 'deleteAction'],
            formatter: function (data, type, full, meta) {
              var html = '<div class="btn-group user" style="width:100%;  margin:0;">' +
                '<span style="  border:1px solid #ccc; background: white; color: #333; text-transform: capitalize;" type="button" class="label label-default dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">' +
                data + '<span class="caret"></span>' +
                '</span>' +
                '<ul class="dropdown-menu" style="cursor:pointer; font-size:12px;  overflow:hidden;">' +
                ' <li class="dt-action dropdown-item" data-action-row="' + meta.row + '" data-action-button="updateStatus" data-action-input="ready"><a style="text-transform: capitalize;">Ready</a></li>' +
                ' <li class="dt-action dropdown-item" data-action-row="' + meta.row + '" data-action-button="updateStatus" data-action-input="draft"><a style="text-transform: capitalize;">Draft</a></li>' +
                '</ul>' +
                '</div>'
              if (full.approvalStatus === 'approved' || full.approvalStatus === 'rejected') {
                html = '<p style="font-size:12px; font-weight:bold; text-align:center; text-transform: capitalize !important; padding:0; margin: 0;">' + data + '</p>'
              }
              return (data ? html : '')
            }
          },
          {
            name: 'approvalStatus',
            data: 'approvalStatus',
            title: 'Approval Status',
            render: function (data, type, full, meta) {
              var label = ''
              if (data === 'approved') {
                label = '<span class="label label-success">Approved</span>'
              } else if (data === 'rejected') {
                label = '<span class="label label-danger">Rejected</span>'
              } else if (data === 'pending') {
                label = '<span class="label label-warning">Pending</span>'
              } else {
                label = ''
              }
              return label
            }
          },
          {
            name: 'bargainStatus',
            data: 'bargainStatus',
            title: 'Bargain Status',
            render: function (data, type, full, meta) {
              if (data === 'outOfStock') {
                data = 'Out of Stock'
              }
              var html = '<div class="btn-group user" style="width:100%; margin:0;">' +
                '<span style="border:1px solid #ccc; background: white; color: #333; text-transform: capitalize;" type="button" class="label label-default dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">' +
                data + '<span class="caret"></span>' +
                '</span>' +
                '<ul class="dropdown-menu" style="cursor:pointer; font-size:12px;  overflow:hidden;">' +
                ' <li class="dt-action dropdown-item" data-action-row="' + meta.row + '" data-action-button="updateBargainStatus" data-action-input="active"><a>Active</a></li>' +
                ' <li class="dt-action dropdown-item" data-action-row="' + meta.row + '" data-action-button="updateBargainStatus" data-action-input="expired"><a>Expired</a></li>' +
                ' <li class="dt-action dropdown-item" data-action-row="' + meta.row + '" data-action-button="updateBargainStatus" data-action-input="outOfStock"><a>Out of Stock</a></li>' +
                '</ul>' +
                '</div>'
              return (data ? html : '')
            }
          },
          {
            name: '',
            title: '',
            render: function (data, type, full, meta) {
              var html = '<span class="label label-warning cursor-pointer custom-padding-right dt-action" title="Edit" data-action-row="' + meta.row + '" data-action-button="editAction"><i class="fa fa-pencil"></i></span>' +
                '<span class="label label-danger dt-action cursor-pointer" title="Remove" data-action-row="' + meta.row + '" data-action-button="deleteAction"><i class="fa fa-trash"></i></span>'
              if (full.approvalStatus === 'approved' || full.approvalStatus === 'rejected') {
                html = ''
              }
              return html
            }
          }
        ]
        return column
      }
    }
  }
</script>

<style>
  .label {
    display: inline;
    padding: .2em .6em .3em;
    font-size: 85%;
    font-weight: bold;
    line-height: 1;
    color: #fff;
    text-align: center;
    white-space: nowrap;
    vertical-align: baseline;
    border-radius: .25em;
  }

  .control.has-icon.has-icon-right > .fa {
    right: 4px;
  }

  div.el-dialog--small {
    width: 100%;
  }

  @media (min-width: 0px) {
    div.el-dialog--small {
      top: 0 !important;
    }

    .row-pad-left {
      padding-left: 0px;
    }

    .row-pad-right {
      padding-right: 0px;
    }
  }

  @media (min-width: 700px) {
    div.my-bargain div.el-dialog__body .container {
      width: 700px;
    }
  }

  @media (min-width: 800px) {
    div.my-bargain div.el-dialog__body .container {
      width: 800px;
    }
  }

  @media (min-width: 825px) {
    div.el-dialog--small {
      width: 825px;
      top: 2% !important;
    }

    .row-pad-left {
      padding-left: 5px;
    }

    .row-pad-right {
      padding-right: 5px;
    }
  }

  @media (min-width: 1000px) {
    div.my-bargain div.el-dialog__body .container {
      width: 1000px;
    }

    div.el-dialog--small {
      width: 1000px;
      top: 2% !important;
    }
  }

  span.label .fa {
    font-size: 11px;
  }

  .col-form-label {
    padding-bottom: 0;
  }

  .custom-padding-right {
    margin-right: 10px;
  }

  span.multiselect__option {
    min-height: 25px;
    line-height: 10px;
    font-weight: normal;
  }

  span.multiselect__option--selected {
    font-weight: bold;
  }

  .multiselect__input, .multiselect__single {
    padding: 0;
    margin: 0;
  }

  .datepicker {
    width: 100%;
  }

  .file-uploads span {
    line-height: 100px;
    font-weight: normal;
  }

  i.fa-file-image-o {
    color: #616161;
  }

  div.my-bargain button.btn {
    padding-top: 5px;
    padding-bottom: 5px;
  }

  div.my-bargain div.pagination button.btn {
    padding-top: 5px;
    padding-left: 10px;
    padding-right: 10px;
    padding-bottom: 5px;
  }

  div.modal-body ul li a.file-text {
    display: block;
    overflow: hidden;
    margin-right: 40px !important;
    text-overflow: ellipsis;
  }

  a.delete-action {
    position: absolute;
    right: 0;
    top: 3px;
    font-size: 12px;
    color: #20a0ff;
    display: none;
  }

  ul.list-group {
    overflow: hidden;
  }

  i.fa-file-image-o {
    margin-right: 5px !important;
  }

  div.upload-hover {
    border: 1px solid #c0ccda;
    height: 100px;
    border-radius: 5px;
  }

  div.upload-hover:hover {
    cursor: pointer;
    border: 1px solid #2196F3;
  }

  div.upload-hover .input-upload {
    cursor: pointer;
  }

  .list-group-item-action {
    cursor: pointer;
    display: block;

  }

  .my-bargain li.list-group-item {
    position: relative;
    height: 25px;
    display: block;
    list-style-position: inside;
    white-space: nowrap;

  }

  .my-bargain li.list-group-item:hover a.delete-action {
    color: #039BE5 !important;
    display: block;
  }

  .my-bargain .list-group-item-action a {
    color: #475669;
  }

  a.delete-action:hover {
    text-decoration: underline !important;
  }

  .my-bargain .list-group-item-action:hover {
    border-radius: 5px;
  }

  .my-bargain .list-group-item-action:hover a.file-text {
    color: #039BE5;
  }

  .datepicker .form-control {
    padding-top: 0;
  }

  div#customModal div.modal-dialog, div.#customModal div.modal-content, div#customEdit div.modal-dialog, div.#customEdit div.modal-content {
    position: relative;
    margin: 0 auto;
  }

  div#customModal div.multiselect__tags, div#customEdit div.multiselect__tags {
    border: none;
    border-bottom: 1px solid #ccc;
    font-weight: normal;
    border-radius: 0;
    min-height: 30px;
    padding-left: 0;
    margin-top: 3px;
    padding-top: 5px;
  }

  .btn-md {
    background: #fff;
    border: 1px solid #ccc;
    border-color: #ccc !important;
  }

  button.btn-md.active {
    background: #025aa5 !important;
    background-color: #039be5 !important;
    color: #fff !important;
  }

  div#customModal div.multiselect__select, div#customEdit div.multiselect__select {
    border-radius: 0;
    top: -2px;
    right: -10px;
  }

  .form-over-error {
    background-image: -webkit-linear-gradient(bottom, #ff3860 2px, rgba(0, 150, 136, 0) 0), -webkit-linear-gradient(bottom, rgba(0, 0, 0, .26) 1px, transparent 0) !important;
    background-image: linear-gradient(0deg, #ff3860 2px, rgba(0, 150, 136, 0) 0), linear-gradient(0deg, rgba(0, 0, 0, .26) 1px, transparent 0) !important;
  }

  .control.has-icon > .fa {
    display: inline-block;
    font-size: 14px;
    height: 24px;
    line-height: 24px;
    text-align: center;
    vertical-align: top;
    width: 24px;
    color: #dbdbdb;
    pointer-events: none;
    position: absolute;
    top: 4px;
    z-index: 4;
  }

  .fa-warning {
    color: #ff3860;
  }

  .label-danger {
    background-color: #f05050;
  }

  .label-success {
    background-color: #27c24c;
  }

  .label-default {
    color: #1f2d3d;
    border: 1px solid #bfcbd9;
    background-color: white;
    cursor: pointer;
  }

  .label-default:hover {
    color: #20a0ff;
    border-color: #20a0ff;
  }

  .label-warning {
    background-color: #ff902b;
  }

  .table > tbody > tr > td {
    padding: 0.60rem 0.75rem !important;
    vertical-align: middle;
  }

  .table > tbody > tr > td a {
    font-size: 12px;
  }

  .table > thead > tr > th {
    font-size: 13px !important;
  }

  div.user .dropdown-menu .dropdown-item {
    width: inherit;
    min-height: 0;
  }
</style>



